.sidebar-container {
  background-color: var(--slack-color);
  color: white;
  flex: 0.3;
  border-top: 1px solid #49274b;
  max-width: 260px;
  margin-top: 48px;
  height: calc(100vh + 20px);
}

.sidebar-container hr {
  margin-top: 10px;
  margin-bottom: 10px;
  border: 1px solid #49274b;
}

.sidebar-header {
  display: flex;
  border-bottom: 1px solid #49274b;
  padding: 13px;
}
.sidebar-header .edit-icon {
  padding: 8px;
  color: #49274b;
  font-size: 18px;
  background-color: white;
  border-radius: 999px;
}
.sidebar-info {
  flex: 1;
}
.sidebar-info h2 {
  font-size: 15px;
  font-weight: 900;
  margin-bottom: 5px;
}

.sidebar-info h3 {
  display: flex;
  font-size: 13px;
  font-weight: 400;
  align-items: center;
}

.status-icon {
  font-size: 14px;
  margin-top: 1px;
  margin-right: 2px;
  color: green;
}


